@import url(https://fonts.googleapis.com/css?family=Open+Sans:300italic,400italic,400,300,700);
@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Anonymous+Pro:400,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:300,300i,400,400i,700,700i|Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i|Istok+Web:400,400i,700,700i&amp;subset=cyrillic,cyrillic-ext,latin-ext);

/* Colours */ 
$telegram:		 #1d93d2;
$twitter:        #6dabda;
$linkedin:       #2b77b1;
$xing:           #006567;
$google-plus:    #c95644;
$facebook:       #365899;
$github:         #f6c2a4;
$stack-overflow: #c98b3b;
$instagram:      #cd486b;
$reddit:         #cee3f8;
$rss:            #eea33a;

$darker:         #21211E;
$dark:           #2D2D29;
$medium:         #3B3B35;
$light-medium:   #969696;
$bright-blue:    #A5F1E4;
$light-blue:     lightseagreen;
$green:          #92C7A3;
$light:          #DFECE6;
$white:          #FFFFFF;
$dark-purple:    #300030;
$medium-purple:  #480048;
$regular-purple: #601848;
$red:            #C04848;
$orange:         #F07241;

/* Responsive sizes */
$screen-sm-min: 768px;
$screen-md-min: 992px;
$screen-lg-min: 1200px;

/* Globals font - ​Tahoma (Roboto)  */

::selection {
	background: $light-blue;
	color: black; 
}
::-moz-selection {
	background: $light-blue;
	color: black; 
}

html {
	height: 100%;
	overflow-x: hidden;
}

body {
	font-family: Istok Web, monospace;
	font-weight: 300;
	background-color: $dark;
	color: $white;
	font-size: 16px;
	height: 100%;
	width: 100%;
}

a, a:active {
	color: $light-blue;
	text-decoration: none;
}

a:hover {
	color: $bright-blue;
	text-decoration: none;
}

img:hover {
	-webkit-transform: scale(1.17);
	-ms-transform: scale(1.17);
	transform: scale(1.17);
	transition: transform .15s ease;
}

p {
    margin-top: 1em;
    margin-bottom: 1em;
}

ul {
    list-style-type: none;
}

pre {
	background-color: none;
}
.typed-cursor {
	color: white;
	margin-left: 7px;
	-webkit-animation: blink 1.3s linear infinite; 
	animation: blink 1.3s linear infinite; 
}
.navbar, .nav {
	background-color: $bright-blue;

	.navbar-toggle {
		margin-top: 10px;
		padding-top: 0px;
		padding-bottom: 20px;
	}

	li {

		a, a:active {
			color: $white;
		}

		a:hover {
			color: $bright-blue;
			text-decoration: none;
		}
	}
}

.clearfix:after {
   content: " ";
   display: block;
   height: 0;
   clear: both;
}

.header {
	font-size: 32px;
	text-align: center;
	margin-top: 20px;
	margin-left: -0%;
	width: 100%;

	@media (min-width: $screen-sm-min) {
		font-size: 62px;
		margin-left: -10%;
		width: 120%;
	}
}
.logo8250 {
    color: white;
    font-family: Helvetica;
    font-size: .7em;
    font-weight: 500;
    left: 2%;
    position: relative;
	text-decoration: none;
}
.menu-button {
    color: #FFFFFF;
    font-size: 24px;
    position: fixed;
    top: 0;
    left: 0;
    cursor: pointer;
    line-height: 1em;
    padding: 27px;
    // background-color: #2D2D29;
    z-index: 100;
	height: 100%;
	
	@media (min-width: $screen-sm-min) {
		padding: 50px;
	}
}

.content-panel, .content-panel-clean {
	background-color: $medium;
	padding-top: 30px;
	padding-bottom: 20px;
	padding-left: 30px;
	padding-right: 30px;
	margin-top: 20px;
	box-shadow: 0px 0px 0px 0px, 0px 6px 12px rgba(0, 0, 0, 0.2);
	border-bottom: 5px solid $light-blue;
	
	@media (max-width: $screen-sm-min) {
		margin-top: 0;
	}

	&.portfolio {
		@media (max-width: $screen-sm-min) {
			padding: 0;
		}
	}
}
.content-panel-clean {
	padding: 0px;
}

#my-menu {

	p {
		margin-bottom: 5px;
	}

	a, a:active {
		color: $white;
	}

	a:hover {
		color: $light-blue;
	}

	.pages {

		.fa {
			margin-right: 10px;
		}
	}
}

.links {
	margin-top: 50px;
	text-align: center;

	i {
		padding-left: 5px;
		padding-right: 5px;

		@media (min-width: $screen-sm-min) {
			padding-left: 10px;
			padding-right: 10px;
		}
	}

	.fa, .fas, .fab {
		-webkit-transition: 0.3s;
		-moz-transition: 0.3s;
		transition: 0.3s;
	}

	.fa-telegram:hover, .fa-telegram-plane:hover {
		color: $telegram;
	}

	.fa-twitter:hover {
		color: $twitter;
	}

	.fa-linkedin:hover {
		color: $linkedin;
	}
	
	.fa-xing:hover {
		color: $xing;
	}

	.fa-google-plus-g:hover, .fa-google-plus:hover {
		color: $google-plus;
	}
	
	.fa-facebook:hover {
		color: $facebook;
	}

	.fa-github-alt:hover {
		color: $github;
	}

	.fa-stack-overflow:hover {
		color: $stack-overflow;
	}
	
	.fa-instagram:hover {
		color: $instagram;
	}
	
	.fa-reddit-alien:hover {
		color: $reddit;
	}
	
	.fa-rss:hover {
		color: $rss;
	}
}
/* Index */

.index {
	height: 100%;
	max-height: 100%;
	width: 100%;
	padding: 0px;

	@media (min-width: $screen-sm-min) {
		width: 700px;
		max-width: 700px;
		padding: 30px 0px 0px 0px;
	}

	.header {
		margin-top: 10px;
		margin-left: 0%;
		margin-bottom: 0px;
		font-size: 32px;
		width: 100%;

		@media (min-width: $screen-lg-min) {
			margin-left: -10%;
			width: 120%;
		}
	}

	.categories {
		font-size: 22px;
	}

	.content-panel:nth-of-type(1) {
		border-bottom: 10px solid $light-blue;
	}
	
	.site-name {
		font-family: Roboto, sans-serif;
		letter-spacing: 2.5px;
		color: $light-blue;
		margin-top: 0px;
		padding-top: 15px;
		font-size: 30px;
		
		text-shadow: 0px 5px 7px #000;
		-moz-text-shadow: 0px 5px 7px #000;
		-webkit-text-shadow: 0px 5px 7px #000;

		.blink {
			&:after {
				content: "_";
				color: white;
				margin-left: 7px;
				-moz-animation: blink 1.3s linear infinite; 
				-webkit-animation: blink 1.3s linear infinite; 
				animation: blink 1.3s linear infinite; 
			}
		}

		@media (min-width: $screen-sm-min) {
			font-size: 52px;
			margin-top: 0px;
		}

		@media (min-width: $screen-md-min) {
			font-size: 60px;
			margin-top: 0px;
		}
	}

	.row {
		height: 100%;
		max-height: 100%;
		margin-top: 0px;
		margin-left: 0px;
		margin-right: 0px;
	}

	.gravatar {
		width: 100%;
		margin-top: -80px;
		text-align: center;
	}

	.author-text {
		text-align: center;
		margin-top: 30px;
		letter-spacing: 0.5px;

		b {
			font-size: 32px;
		}

		// Пример разделения li элементов

		// .portfolio-social li:not(:last-child)::after {
			// 	height: 40px;
			// 	background-color: #dee2e6;
		// }
		// .footer-social li:not(:last-child)::after, .portfolio-social li:not(:last-child)::after {
			// 	content: '';
			// 	position: absolute;
			// 	top: 50%;
			// 	right: 0px;
			// 	-webkit-transform: translate(50%, -50%) rotate(25deg);
			// 	transform: translate(50%, -50%) rotate(25deg);
			// 	width: 2px;
			// 	height: 100%;
			// 	background-color: #dee2e6;
		// }
	}

	.links {
		font-size: 2em;
	}

	.articles {
		text-align: center;
		font-size: 20px;

		h2 {
			margin-bottom: 30px;
			color: $light-blue;
			font-size: 26px;
		}

		ul {

			padding: 0px;

			li {
				margin-bottom: 20px;
				font-size: 24px;

				a:hover {
					color: $light-medium;
					text-decoration: none;
				}

				small {
					font-size: 14px;
					margin-top: -10px;
					padding-top: 0px;
					color: $light-medium;
				}

			}

			li:nth-last-of-type(1) {

				small {
					font-size: 16px;
				}

			}
		}

		a, a:active {
			color: $white;
			white-space: nowrap;
		}

		a:hover {
			color: $light-blue;
			text-decoration: none;
		}
	}

	.copyright {
		margin-top: 57px 0 0;
		text-align: center;
	}

	.links {
		a, a:active {
			color: #FFFFFF;
		}
	}

	.menu {
		margin-top: 250px;
		margin-bottom: 20px;
		font-size: 20px;
		position: absolute;
		bottom: 0px;

		a, a:active {
			color: #FFFFFF;
		}

		a:hover {
			color: $light-blue;
		}

		ul {
			margin-bottom: 30px;
			padding-left: 10px;

			ul {
				padding-left: 50px;
				list-style: none;
				margin-left: 0;
				text-indent: -0.6em;
			}
		}
	}

}

.container-wide {
	
	padding: 30px 0px 0px 0px;
	width: 970px;
	max-width: 970px;

	@media (max-width: $screen-md-min) {
		width: 570px;
	}
	@media (max-width: $screen-sm-min) {
		width: 100%;
	}
	
	.header {
		margin-top: 10px;
		margin-left: 0%;
		margin-bottom: 0px;
		font-size: 32px;
		width: 100%;

		@media (min-width: $screen-lg-min) {
			margin-left: -10%;
			width: 120%;
		}
	}
}

.all-posts {
	margin-left: 0px;
	margin-right: 0px;

	@media (min-width: $screen-sm-min) {
		#search-container {
			padding-top: 75px;
			padding-bottom: 150px;
		}
	}

	@media (min-width: $screen-md-min) {
		margin-left: -15px;
		margin-right: -15px;

		#search-container {
			padding-bottom: 300px;
		}
	}

	#search-input {
		position:relative;
		width: 100%;
		padding:6px 15px 6px 30px;
		margin: 2px;
		margin-top: 50px;

		color:$dark;
		background-color:$white;

		border: medium none;
		border-radius:15px;
		-moz-border-radius:15px;
		-webkit-border-radius:15px;
	}

	#search-input:focus {
		border: none;
  }

	.content-panel {
		padding-left: 10px;
		padding-right: 10px;
		@media (min-width: $screen-md-min) {
			padding-left: 30px;
			padding-right: 30px;
		}
	}
}

.blog-post {
    height: 19px;
    text-align: right;
}

.thumb {
	margin-top: 20px;
	margin-bottom: 0px;
	text-align: center;

	img {
		height: 50px;
		fill: #fff;
		
		@media (min-width: $screen-sm-min) {
			height: 70px;
		}
	}
}

.single {
	height:100%;
	max-height: 100%;

	h1, h2, h3, h4, h5, h6 {
		font-family: 'Roboto Condensed', sans-serif;
		margin-bottom: 23px;
	}

	h2 {
		font-size: 36px;
	}

	h3 {
		font-size: 28px;
	}

	p {
		margin-bottom: 10px;

		@media (min-width: $screen-sm-min) {
			margin-bottom: 30px;
		}
	}

	.menu {

		background-color: $bright-blue;

		a, a:active {
			color: $white;
		}

		a:hover {
			color: $light-blue;
		}

		h4 {
			margin-top: 20px;
		}

		.share {
			margin-top: 20px;

			h4 {
				margin-bottom: 10px;
			}
		}

		.home {
			margin-top: 10px;
			font-size: 16px;

			a {
				color: $white;
			}
		}

		.related {
			ul {
				padding: 0px;
			}
			li {
				margin-bottom: 10px;
			}
		}
	}

	.article {
		width: 100%;
		margin-left: 0%;
		padding: 0px;

		@media (min-width: $screen-sm-min) {
			width: 90%;
			margin-left: 5%;
		}

		@media (min-width: $screen-md-min) {
			width: 70%;
			margin-left: 15%;
		}

		@media (min-width: $screen-lg-min) {
			width: 50%;
			margin-left: 25%;
		}

		.author {
			font-size: 20px;
			text-align: center;
			margin-bottom: 0px;
		}

		.read-time {
			font-size: 16px;
			text-align: center;
			margin-bottom: 10px;
			font-weight: 400;
			@media (min-width: $screen-sm-min) {
				margin-bottom: 40px;
			}
		}
		.read-next {
			padding: 5px 0;
		}

		.content {
			font-size: 15px;
			line-height: 170%;
			padding-left: 15px;
			padding-right: 15px;
			padding-top: 10px;
			padding-bottom: 10px;

			@media (min-width: $screen-sm-min) {
				font-size: 17px;
				padding-left: 30px;
				padding-right: 30px;
				padding-top: 40px;
				padding-bottom: 20px;
			}

			.series {
				margin-bottom: 50px;
			}

			img {
				max-width: 100%;
				padding: 10px;
    		display: block;
    		margin-left: auto;
    		margin-right: auto
			}

			ul {
				list-style-type: disc;
			}

            		ol {
				list-style-type: decimal;
			}

			blockquote {
				position: relative;
				width: 100%;
		    margin-left: -15px;
				padding-right: 10px;
				background-color: $darker;
				color: white;
				box-sizing: content-box;
				border: none;
				font-size: 28px;

				@media (min-width: $screen-sm-min) {
					margin-left: -30px;
					padding-left: 45px;
					padding-right: 15px;
				}

				p {
			    margin: 0;
			    padding: 15px;
					z-index: 0;
				}

				:before {
    			font-family: "Times New Roman";
					color: $light-medium;
			    content: "“";
			    position: absolute;
			    top: 15px;
			    left: 15px;
			    font-size: 65px;
			    font-weight: bold;
				}

			}

			.alignright {
				float: right;
			}

			.aligncenter {
			    display: block;
			    margin: 5px auto 5px auto;
			}

			figure.highlight, div.highlighter-rouge {
				width: 100%;
				margin-left: -15px;
				padding-right: 10px;
				box-sizing: content-box;

				@media (min-width: $screen-sm-min) {
					margin-left: -30px;
					padding-right: 60px;
				}

				pre {
					width: 100%;
					font-family: 'Anonymous Pro';
					background-color: $darker;
					color: $white;
					font-size: 12px;
					border: none;
					padding-left: 15px;
					padding-top: 20px;
					padding-bottom: 20px;
					padding-right: 15px;
					box-sizing: content-box;
					border-radius: 0;
					margin-bottom: 10px;
					margin-top: 10px;

					@media (min-width: $screen-sm-min) {
						padding-left: 50px;
						padding-right: 10px;
						margin-bottom: 40px;
						font-size: 15px;
					}
				}
			}

			p, ul, ol {

				code {
					font-family: 'Anonymous Pro';
					font-size: 15px;
					background-color: $darker;
					color: $white;
					padding: 0.5em;
					vertical-align: middle;
				}

			}

			.tags {
				margin-top: 10px;
				margin-bottom: 10px;
				text-align: center;

				a, a:active {
					color: $white;
				}

				a:hover {
					color: $light-blue;
				}

			}

			.share {
				margin-top: 20px;
				margin-bottom: 10px;
				text-align: center;

			}

		}

		.feedback {
			text-align: center;

			.fa-heart {
				color: $red;
			}

		}
	}
}

.related {
	margin-top: 50px;
	text-align: center;

	.related-header {
		margin-top: -30px;
		margin-bottom: 20px;

		a {
			color: $white;
			font-size: 18px;
			width: auto;
			padding: 10px 20px;
			background-color: $darker;
			box-shadow: 0px 0px 0px 0px, 0px 6px 12px rgba(0, 0, 0, 0.2);
		}

		a:hover {
			color: $bright-blue;
		}
	}

	.title {
		padding-top:20px;
		font-size: 24px;
		margin-bottom: 20px;

		@media (min-width: $screen-sm-min) {
			font-size: 36px;
		}

		a {
			color: $white;
		}

		a:hover {
			color: $light;
		}
	}

	p {
		margin-bottom: 0px;
	}

	.previous-next {
		width: 50%;

		a {
			color: $white;
		}

		a:hover {
			color: $light;
		}
	}

	.next {
		float: right;
		text-align: right;
	}

	.previous {
		float: left;
		text-align: left;
	}

	.date {
		color: $light-medium;
	}
}

.search-hr {
	width: 700px;
	border-color: $light-medium;
}

.error {
	.error-text {
		margin-top: 30px;

		a {
			color: $light-blue;
			font-size: 14px;
		}
	}

	.links {
		font-size: 1em;
	}
}

.footer {
	width: 100%;
	margin-left: 0%;

	.fa-heart {
		color: $red;
	}

	@media (min-width: $screen-sm-min) {
		width: 90%;
		margin-left: 5%;
	}

	@media (min-width: $screen-md-min) {
		width: 80%;
		margin-left: 10%;
	}

	@media (min-width: $screen-lg-min) {
		width: 70%;
		margin-left: 15%;
	}

	div {
		color: $light-medium;
		text-align: center;
		margin-top: 20px;
		margin-bottom: 20px;
	}
}

.btn-categories {
	background-color: $light-blue;
	border-color: $light-blue;
	color: $white;
	&:hover {
		background-color: $bright-blue;
		border-color: $bright-blue;
		color: $white;
	}
	&:active {
		background-color: $bright-blue;
		border-color: $bright-blue;
		color: $white;
	}
	&:focus {
		background-color: $bright-blue;
		border-color: $bright-blue;
		color: $white;
	}
}

.dropdown-menu {
	background-color: $light-blue;
	color: $white;
	> li > a {
		background-color: $light-blue;
		color: $white;
		&:hover {
			background-color: $bright-blue;
			color: $white;
		}
	}
}

table{
    width: 100%;
    max-width: 100%;
	margin-bottom: 20px;
	border: 1px solid #ddd;
	background-color: transparent;
}
.table>tbody>tr>td, 
.table>tbody>tr>th, 
.table>tfoot>tr>td, 
.table>tfoot>tr>th, 
.table>thead>tr>td, 
.table>thead>tr>th {
    padding: 8px;
    line-height: 1.42857143;
    vertical-align: top;
    border-top: 1px solid #ddd;
}

th{
    padding: 13px 15px;
    border:0px solid $darker;
}

td{
    padding: 13px 15px;
    border: 0px solid #456862;
}
.main {
    background-image: url("/images/inpost/shutterstock_238100266-979x1024.jpg");
    background-size: cover;
	padding: 0;
}
.gradient {
    color: white;
    background-image: radial-gradient(rgba(255,255,255,0.3), rgba(4,4,4,0.9));
    padding: 170px 40px 45px;
}

.portfolio {
	text-align: center;
	background-color: transparent;
	box-shadow: none;
}
.grid {	
	display: grid;
	grid-gap: 15px;

	@media (max-width: $screen-lg-min) {
		grid-template-columns: 1fr 1fr;
	}

	@media (max-width: $screen-md-min) {
		grid-template-columns: 1fr;
	}

	grid-template-columns: 1fr 1fr 1fr;
	color: #bdbdbd;
	padding: 25px 0;

	.grid-item {
		background-position: center top;
