.grid-item {
    width: 100%;
    overflow: hidden;
	position: relative;
    min-height: 350px;
    max-height: 550px;
	cursor: pointer;
}

.grid-item img {
    -moz-filter: grayscale(70%) blur(3px);
    -webkit-filter: grayscale(70%) blur(3px);
    filter: grayscale(70%) blur(3px);
    margin: 0 auto;
    top: 0;
    left: 0;
	position: absolute;
	width: inherit;
}

.grid-item:hover img {    
	-moz-filter: none;
	-webkit-filter: none;
	filter: none;
	transition: filter .5s ease;
}

.portfolio .grid-item img:hover {
	transform: none;
}



.work-title {
    position: absolute;
    width: 100%;
    line-height: 47px;
    vertical-align: middle;
    top: calc(50% - 45px);
    left: 0;
    font-weight: 400;
    font-size: 21px;
    text-align: center;
    text-transform: uppercase;
    color: #52c6bf;
    z-index: 1;
    transition: top .5s ease;
}

.grid-item:hover .work-title {
	top: -155px;
	text-shadow: none;
	color: $light-blue;
}

.grid-item:hover .button {
	opacity: 1;
}

.button {
    position: absolute;
    width: 100%;
    left: 0;
    bottom: 0;
    text-align: center;
    opacity: 0;
    transition: opacity .35s ease;
    background: #444;
    height: 40px;
    padding: 10px 0 0;
}

.button a {
    width: auto;
    padding: 10px 15px;
    text-align: center;
    color: $light-blue;
    z-index: 1;
}

.button a:first-child {
    border-right: 1px solid white;
}

@keyframes blink {
	0% { opacity:1; }
	50% { opacity:0; }
	100% { opacity:1; }
}
@-webkit-keyframes blink {
	0% { opacity:1; }
	50% { opacity:0; }
	100% { opacity:1; }
}
@-moz-keyframes blink {
	0% { opacity:1; }
	50% { opacity:0; }
	100% { opacity:1; }
}
@-ms-keyframes blink {
	0% { opacity:1; }
	50% { opacity:0; }
	100% { opacity:1; }
}
@-o-keyframes blink {
	0% { opacity:1; }
	50% { opacity:0; }
	100% { opacity:1; }
}
